/**
 * List of colour variables for the different parts of the flowchart
 */
.node:focus {
  outline: none; }
  [data-whatinput='keyboard'] .node:focus rect {
    stroke-width: 3.5px;
    stroke: rgba(0, 191, 255, 0.85); }

.node text {
  font-weight: 600;
  font-size: 1.4em;
  transition: all ease 0.1s;
  pointer-events: none; }
  .kui-theme--light .node text {
    fill: rgba(51, 51, 51, 0.9); }
  .kui-theme--dark .node text {
    text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    fill: rgba(255, 255, 255, 0.8); }

.node rect {
  stroke-width: 1px;
  transition: all ease 0.1s; }
  .kui-theme--light .node rect {
    fill: white;
    stroke: #919191; }
  .kui-theme--dark .node rect {
    fill: #2c4251;
    stroke: #5f686e; }

.kui-theme--light .node--highlight text, .kui-theme--light
.node--active text, .kui-theme--light
.node--selected text {
  fill: #333; }

.kui-theme--dark .node--highlight text, .kui-theme--dark
.node--active text, .kui-theme--dark
.node--selected text {
  fill: white; }

.node--active rect {
  stroke-width: 2px; }
  .kui-theme--light .node--active rect {
    fill: white;
    stroke: #464646; }
  .kui-theme--dark .node--active rect {
    fill: #314a5b;
    stroke: #d1d4d6; }

.node--selected rect {
  stroke-width: 3px; }
  .kui-theme--light .node--selected rect {
    fill: white;
    stroke: #333; }
  .kui-theme--dark .node--selected rect {
    fill: #3e5c72;
    stroke: white; }

.node--faded {
  opacity: 0.3; }

.node--icon text {
  opacity: 0;
  pointer-events: none; }

.node__icon {
  pointer-events: none; }
  .kui-theme--light .node__icon {
    fill: #333; }
  .kui-theme--dark .node__icon {
    fill: white; }

/**
 * List of colour variables for the different parts of the flowchart
 */
.edge path {
  pointer-events: none;
  fill: none;
  stroke-width: 1.5px; }
  .kui-theme--light .edge path {
    stroke: #626262; }
  .kui-theme--dark .edge path {
    stroke: #dddfe0; }

.edge--faded {
  opacity: 0.1; }

.kui-theme--light .pipeline-flowchart__arrowhead {
  fill: #626262; }

.kui-theme--dark .pipeline-flowchart__arrowhead {
  fill: #dddfe0; }

/**
 * List of colour variables for the different parts of the flowchart
 */
.layer {
  opacity: 0;
  transition: opacity ease 0.5s; }
  .kui-theme--dark .layer {
    fill: rgba(255, 255, 255, 0.05); }
  .kui-theme--light .layer {
    fill: rgba(0, 0, 0, 0.05); }
  .layer:hover {
    opacity: 1; }

.pipeline-flowchart__layer-names {
  position: absolute;
  top: 0;
  left: 0;
  width: 130px;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  pointer-events: none; }
  .kui-theme--dark .pipeline-flowchart__layer-names {
    background: linear-gradient(to right, #1a2730, transparent); }
  .kui-theme--light .pipeline-flowchart__layer-names {
    background: linear-gradient(to right, #eee, transparent); }

.layer-name {
  position: absolute;
  top: -10px;
  display: flex;
  align-items: center;
  height: 20px;
  padding-left: 15px;
  font-weight: bold;
  font-size: 1.6em;
  white-space: nowrap; }

.pipeline-flowchart {
  height: 100%; }

.pipeline-flowchart__graph {
  display: block; }
